import {
  EthereumClient,
  w3mConnectors,
  w3mProvider,
} from "@web3modal/ethereum";
import { Web3Modal } from "@web3modal/react";
import {
  configureChains,
  createConfig,
  useAccount,
  usePublicClient,
  useWalletClient,
  WagmiConfig,
} from "wagmi";
import { Web3Button } from "@web3modal/react";
import { mainnet, polygon, polygonMumbai } from "wagmi/chains";
import { useEffect, useMemo, useState } from "react";
import JSONPretty from "react-json-pretty";
import SecurityToken from "security-token-artifacts/contracts/SecurityToken.json";

const chains = [mainnet, polygon, polygonMumbai];
const projectId = "d7f156f22c6a01b54985f90a18fc0193";

const { publicClient } = configureChains(chains, [w3mProvider({ projectId })]);
const wagmiConfig = createConfig({
  autoConnect: true,
  connectors: w3mConnectors({ projectId, chains }),
  publicClient,
});
const ethereumClient = new EthereumClient(wagmiConfig, chains);

export default function App() {
  return (
    <>
      <WagmiConfig config={wagmiConfig}>
        <HomePage />
      </WagmiConfig>

      <Web3Modal projectId={projectId} ethereumClient={ethereumClient} />
    </>
  );
}

function HomePage() {
  const { isConnected } = useAccount();
  const { data: wallet } = useWalletClient();
  const client = usePublicClient();

  const [address, setAddress] = useState<string>();
  const [tokenData, setTokenData] = useState<Object>();

  useEffect(() => {
    if (isEthereumAddress(address)) {
      Promise.all([
        client.readContract({
          address: address as `0x${string}`,
          abi: SecurityToken.abi,
          functionName: "name",
        }),
        client.readContract({
          address: address as `0x${string}`,
          abi: SecurityToken.abi,
          functionName: "symbol",
        }),
        client.readContract({
          address: address as `0x${string}`,
          abi: SecurityToken.abi,
          functionName: "totalSupply",
        }),
        client.readContract({
          address: address as `0x${string}`,
          abi: SecurityToken.abi,
          functionName: "cap",
        }),
      ]).then(([name, symbol, supply, cap]) =>
        setTokenData({
          name,
          symbol,
          cap: (cap as bigint).toString(),
          supply: (supply as bigint).toString(),
        })
      );
    }
  }, [address, client]);

  const styles = useMemo(
    () => (isEthereumAddress(address) ? { color: "green" } : { color: "red" }),
    [address]
  );

  return (
    <>
      <Web3Button />

      {isConnected && (
        <>
          <h4>Connected to {wallet?.chain.name || "loading..."}</h4>
          <input
            value={address}
            style={styles}
            onChange={(e) => setAddress(e.target.value)}
          />
          <JSONPretty id="json-pretty" data={tokenData} />
        </>
      )}
    </>
  );
}

function isEthereumAddress(address?: string): boolean {
  if (!address) return false;

  if (!/^0x[0-9a-fA-F]{40}$/.test(address)) {
    // Address is not the right length or format
    return false;
  }

  // Ensure the address is not all zeros or all ones
  const isZeroAddress = /^0x0+$/.test(address);
  const isMaxAddress = /^0xF+$/.test(address);
  if (isZeroAddress || isMaxAddress) {
    return false;
  }

  return true;
}
